
.addSourceNetwork {
    width: 100%;
}


.uploadedAssetsGrid{
    flex: 0 0 auto;
    width: 50%;
}

.addSourceNetwork,
.innerModalAside {
    width: 100%;
}

.addSource .uppy-Container {
    z-index: 1;
}

.addSource .uploadInnerAside .uppy-Container {
    z-index: 9999;
}

.assetsModalScroll{
    height: calc(100vh - 45px);
    overflow-y: auto;
}
.uploadedAssetsGrid{
    width: 50%;
}

.uploadInnerAside{
    width: 100%;
    margin-right: 0;
}
.onsite_preview_inner {
    position: relative;
    padding-bottom: 400px;
    background-color: #e8e8e8;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    
}

.upload_aside_scroll{
    height: auto;
    padding-right: 0;
}

.onsite_preview_inner .onsite_submit_post_main {
    position: absolute;
    overflow: hidden;
    transform: scale(0.5);
    height: auto;
    top: -133px;
    width: 150%;
    left: -25%;
}

.addSource .uppy-Dashboard-innerWrap{
    box-shadow: none;
}

.addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
    height: 400px;
    min-height: 300px;
}

.addSourceTabs .tab-pane .position-relative i{
    font-size: 18px !important;
}

.addSourceTabs .uppy-DragDrop-inner{
    width: 100%;
}
@media (min-width: 576px){
    .uploadedAssetsGrid{
        width: 33.33%;
    }
}

@media (min-width: 768px){
    .addSourceNetworkList{
        height: calc(100vh - 310px);
        min-height: 480px;
    }
    .uploadedAssetsGrid{
        width: 33.33%;
    }

    .onsite_preview_inner .onsite_submit_post_main {
        top: -38%;
        width: 150%;
        left: -25%;
    }
    .uploadInnerAside{
        width: 240px;
        margin-right: 15px;
    }
    .upload_aside_scroll{
        height: calc(100vh - 425px);
        min-height: 430px;
        padding-right: 15px;
    }
}

@media (min-width: 992px){
    .addSource .uppy-Dashboard-inner .uppy-Dashboard-innerWrap{
        height: calc(100vh - 350px);
        min-height: 450px;
    }
    .assetsModalScroll{
        height: calc(100vh - 96px);
    }
    .addSourceTabs{
        min-height: calc(100vh - 300px);;
    }
    .addSourceNetwork,
    .innerModalAside {
        width: 300px;
    }
    .uploadedAssetsGrid{
        width: 33.33%;
    }
    .innerModalAside{
        position: sticky;
        top: 0;
    }
    .innerModalAsideWrapper {
        height: calc(100vh - 190px);
    }
    .addAssetsModal {
        width: calc(100% - 84px);
        right: 0;
        left: auto;
        height: calc(100% - 50px);
        top: 50px;
        z-index: 95;
        overflow: hidden;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 133%;
    // }
}

@media (min-width: 1280px){
    .uploadedAssetsGrid{
        width: 25%;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 114%;
    // }
}

@media (min-width: 1366px){
    .uploadedAssetsGrid{
        width: 20%;
    }
}
@media (min-width: 1600px){
    .uploadedAssetsGrid{
        width: 16.6667%;
    }
    // .onsite_preview_inner {
    //     padding-bottom: 70%;
    // }
}

@media (min-width: 1920px){
    .uploadedAssetsGrid{
        width: 14.2857%;
    } 
}

