.favorite_icon{
    transition: all .3s;
}
.favorite_icon.active{
    animation:scale-up-center 0.3s;
}

#influList .dropdown-menu{
    min-width: 17rem;
    max-width: 20rem;
}
table .influ_post_slide .position-absolute.end-0.top-0.p-2 {
    padding: 0.3rem !important;
    margin-top: -4px;
}
table .influ_post_slide .assets_video_ico {
    margin-top: -7px;
    font-size: 14px !important;
}
.influ_post_modal .splide__slide .influ_content{
    height: 70px;
}
.influ_post_modal .splide__slide.is-active .influ_content {
    height: auto;
}

.enflu_details .ql-bubble .ql-tooltip,
.enflu_details .ql-bubble .ql-picker-options {
    background-color: rgb(43, 43, 64);
}

@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
      }
      90% {
          -webkit-transform: scale(1.25);
                  transform: scale(1.25);
        }
      100% {
        -webkit-transform: scale(1);
                transform: scale(1);
      }
}
@keyframes scale-up-center {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    90% {
        -webkit-transform: scale(1.25);
                transform: scale(1.25);
      }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

.influ_post_slide .splide__arrow--prev {
    left: -1em;
}
.influ_post_slide .splide__arrow--next {
    right: -1em;
}

.influ_post_slide .splide__arrow:disabled,
.influ_post_modal .splide__arrow:disabled{
    display: none;
}

.influ_post_slide .splide__arrow {
    background: var(--bs-secondary);
    opacity: 1;
    height: 1.8em;
    width: 1.8em;
    box-shadow: var(--bs-dropdown-box-shadow);
}
.influ_post_slide .splide__arrow svg {
    fill: var(--bs-dark);
    height: 0.8em;
    width: 0.8em;
}

.influ_post_slide .splide__arrow:focus-visible {
    outline:none;
    outline-offset: 0px;
}

.enflu_details.offcanvas.offcanvas-end{
    width: 100vw;
}

.enflu_details .offcanvas-header .btn-close {
    position: absolute;
    top: 0px;
    width: 2em;
    height: 2em;
    left: auto;
    right: 5px;
    top: 5px;
}

.symbol-influ{
    height: 70px;
    width: 70px;
}

.creator_label svg path{
    fill:var(--bs-success);
}

.creator_label.bg-dark svg path{
    fill:var(--bs-dark);
}


.influ_filter .rc-slider-step {
    display: none;
}

.influ_filter .rc-slider-handle-dragging.rc-slider-handle-dragging.rc-slider-handle-dragging {
    border-color: #313a53;
    box-shadow: 0 0 0 5px rgba(49, 58, 83, 0.34);
}

#genderChart .apexcharts-pie .apexcharts-series path{
    filter: none !important;
}

#genderChart .apexcharts-legend-marker{
    top: 2px !important;
}

.influ_post_modal .btn-close {
    right: 15px;
    top: 15px;
    z-index: 6;
}

.influ_post_modal .splide__arrow{
    top: 15rem;
}
#genderChart .apexcharts-tooltip-text-goals-value, #genderChart .apexcharts-tooltip-text-y-value, #genderChart .apexcharts-tooltip-text-z-value {
    margin-left: 0px;
}

.asyncLocation .selectpicker__indicator svg{
    display: none;
}
.asyncLocation .selectpicker__dropdown-indicator:before{
    content: "\f0d1";
    font-family: "remixicon" !important;
    font-size: 20px;
    color: var(--bs-text-muted);
}
.asyncLocation .selectpicker__dropdown-indicator {
    padding: 2px 10px;
}

.join_community_blur_bg {
    filter: blur(6px);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
}

.join_community{
    min-height: calc(100vh - 445px);
    background-color: rgba(var(--bs-white-rgb), .5);
    z-index: 2;
}
.join_community .list-points li:before{
    font-family: remixicon!important;
    background-image: none;
    top: 0px;
    left: 0;
    font-size: 16px;
    width: 18px;
    height: 18px;
    color: var(--bs-text-muted);
}
.join_community .list-points li.in_s_1:before{
    content: "\efe0";
}
.join_community .list-points li.in_s_2:before{
    content: "\f25e";
}
.join_community .list-points li.in_s_3:before{
    content: "\ef89";
    transform: scaleX(-1);
    margin-left: -4px;
}
.join_community .list-points li.in_s_4:before{
    content: "\f18b";
}

.quill_editor {
    flex-grow: 1;
    display: flex;
    height: calc(100% - 33px);
}
@media (min-width: 576px) {
.symbol-influ{
    height: 130px;
    width: 130px;
}
.influ_filter .col-lg-8.col-sm-7 .col-lg-5.border-bottom{
    border-bottom: none !important;
}
.influ_post_modal .btn-close{
    right: -30px;
    top: 0;
}
}

@media (min-width: 992px) {
.enflu_details.offcanvas.offcanvas-end{
    width: 65vw;
}

.enflu_details .offcanvas-header .btn-close {
    left: -26px;
    right: auto;
}
.similarCreatores{
    height: calc(100vh - 425px);
}

.influ_filter .col-lg-8.col-sm-7 .col-lg-7.border-bottom{
    border-bottom: none !important;
}

.enflu_details.offcanvas.offcanvas-end {
    box-shadow: var(--bs-offcanvas-box-shadow);
}

.join_community_msg{
    width: 85%;
}

}
@media (min-width: 1280px) {
.send_mail_inst{
    min-height: calc(100vh - 560px);
}
}
@media (max-width: 991px) {
.influ_filter .creatorsFilter .row .border-start.col-lg-7,
.influ_filter .creatorsFilter .row .border-start.col-lg-5.col-sm-12 {
    border-left: none !important;
}

.influ_filter .gx-0.h-100.row .col-lg-7.col-sm-12 {
    border-top: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
    border-bottom: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
}
}
@media (max-width: 575px) {
    .influ_filter .border-start{
        border-left: none !important;
    }
}

