

.rules_elements{
    max-width: 320px;
    min-height: calc(100vh - 220px);
}

.add_more_rule:after,
.add_more_rule:before,
.add_rule:before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    margin: 0 auto;
    width: 1px;
    height: 25px;
    background-color: var(--bs-gray-300);
}

.add_more_rule:before{
    top: auto;
    bottom: 100%;
    height: 15px;
}

.add_rule:before{
    height: 30px;
}

.add_rule:after {
    content: "\ea4e";
    position: absolute;
    left: 0;
    right: 0;
    font-size: 16px;
    text-align: center;
    margin: 0 auto;
    bottom: -42px;
    color: var(--bs-gray-300);
    font-family: remixicon !important;
}

.add_more_rule {
    margin-top: 15px;
    margin-bottom: 25px;
}

.add_rule{
    margin-bottom: 38px;
}

.action_gap{
    height: 10px;
    width: 1px;
    margin: 0 auto;
    left: 0;
    right: 0;
    background-color: var(--bs-gray-300);
}

.add_more_rule .dropdown-menu{    
    left: -47% !important;
}
@media (min-width: 992px) {
.rule_guide{
    border-left: 1px solid var(--bs-border-color-translucent);
}
.rule_guide{
    max-width: 450px;
}
}